.masukan{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 90vh;
}

#petunjuk{
    font-family: Helvetica, sans-serif;
    font-weight: bold;
    font-size: xx-large;    
}

#sname{
    border: 2px solid black;
    width: 10em; 
    font-size: xx-large;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
    position: relative;
    bottom: 20px;
}

input:focus{
    outline: none;
}

#enter{
    width: 70px;
    height: 40px;
    font-family: Helvetica, sans-serif;
    font-weight: bold;
    font-size: large;
    background: radial-gradient(ellipse at center, #3BD56F, #8FD02B);
    border: 2px solid black;
    position: relative;
    bottom: 15px;
}

button:active {
outline: none;
text-decoration: none;
background-color: #3e8e41;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

button:focus {
    outline: none;   
}

button:hover {
    cursor: pointer;
}

.header {
    background: linear-gradient(to right, blue, yellow);
    z-index: 1;
    height: 7vh;
    width: 100%;
    margin-bottom: 20px;
    position: static;
    border-bottom: 2px solid black;
    flex-flow: row nowrap; 
    justify-content: flex-end; 
    align-items: center;
    display: flex;
}

.header p{
    z-index: 2;
    font-family: Helvetica, sans-serif;
    flex-basis: 10%;
    display: flex;
    justify-content: center;
}

.header p:hover{
    background-color: #F5A417 ;
    border-radius: 25px;
    width: auto;
    height: auto;
    cursor: pointer;
}

.Opening {
    display: flex;
    font-family: Helvetica, sans-serif;
    font-weight: bold;
    width: 400px;
    align-items: center;
    white-space: nowrap;
    flex-direction: column;
    margin: 0 auto;
    text-align: center;

}

.fotomuka {
    height: 300px;
    width: 300ox;
    border: 2px solid black;
    border-radius: 25px ;
    position: static;
        
}

body {  
    z-index: 0;
    height: 100vh;
    width: 100%;
    background: radial-gradient(ellipse at center, #3BD56F, #8FD02B);
    margin:0;
    padding: 0;
}

@media screen and (max-width: 915px){
    .header .mobile{
        display:flex;
        flex-basis: 50%;
    }
    .header .desktop{
        display: none;
    }

    
}

@media screen and (min-width: 915px){
    .header .mobile{
        display:none
    }     
}



.fade-appear {
    opacity: 0;
    z-index: 1;
}

.fade-appear.fade-appear-active {
    opacity: 1;
    transition: opacity 600ms linear
}

.fade-enter {
    opacity: 0;
    z-index: 1;
}

.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 600ms linear
}

/*exit*/
.fade-exit {
    opacity: 0;
    z-index: 1;
}

.fade-exit.fade-exit-active {
    opacity: 1;
    transition: opacity 600ms linear
}

.fade-exit-done {
    opacity: 0;
}


.container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-flow: row wrap;
  }
  
  .bio {
    width: 500px;
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Arial, Helvetica, sans-serif;
  }
  
  
  
  .box a:hover {
    font-size: large;
  }
  
  
  
  .box {
    display: flex;
    align-items: center;
    justify-content:flex-start;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    width: 300px;
  }
  
  .box a {
    margin-left: 10px;
    color: #3a3ac6;
    text-decoration: none; 
  }
  
.email {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid black;
  border-radius: 20px;
  flex-direction: column;
  padding: 0;
  background-color:lavender;
  font-family: Helvetica, sans-serif;
  font-weight: bold;
  width: 500px;
  height: 500px;
  }
  
.email p {
    font-weight:bolder;
    font-size: larger;
  }
  
.inputbox {
    margin-top: 10px;
    margin-bottom: 10px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(8, 50px);
    grid-template-areas: 
    "labelname name"
    "labelemail emailing"
    "labelmessage message"
    ". message"
    ". message"
    ". message"
    ". message"
    "submit submit"
    ;
  }
  
  .head {
    grid-area: head;
  }
  
  .labelname {
    grid-area: labelname;
  }
  
  .name {
    grid-area: name;
    align-self:flex-start;
  }
  
  .labelemail {
    grid-area: labelemail;
  }
  
  .emailing {
    grid-area: emailing;
    align-self: flex-start;
  }
  
  .labelmessage {
    grid-area: labelmessage;
  }
  
  .message {
    grid-area: message;
  }
  
  .submit-email{
    grid-area: submit;
    margin-bottom: 10px;
    background-color: greenyellow;
    margin-top: 10px;
    cursor: pointer;
  }
  
  .submit-email:hover{
    box-shadow: 0 5px 16px 0 rgba(0,0,0,0.24), 0 10px 50px 0 rgba(0,0,0,0.19);
  }

  .submit-email:active {
        outline: none;
        text-decoration: none;
        background-color: #3e8e41;
        box-shadow: 0 5px #666;
        transform: translateY(4px);
    }

  
  .email .inputbox input,
  .email .inputbox textarea {
    border: 1px solid green;
    border-radius: 2px;
    padding: 5px 0;
    outline: none;
    font-family: Helvetica, sans-serif;
    font-weight: 500;
    resize:none;
  }